      <h4 class="fw-bold">Pakt Sikorski – Majski: sukces czy zaprzepaszczona szansa?</h4>

      <p>
        <b>Pułkownik Antoni Łapiński, zesłaniec i żołnierz 2 Korpusu Polskiego dobrze zapamiętał dzień, w którym
          dowiedział
          się
          o podpisaniu 30 lipca 1941 roku polsko-sowieckiego paktu: „Dla nas to był cud, to było jak zmartwychwstanie, ale
          przede wszystkim była to dodatkowa kromka chleba. Niewyobrażalny skarb dla cierpiących głód”.</b>
      </p>
      <p>
        Antoni
          Łapiński  wspomina także przemowę kierownika budowy, na której od kilku miesięcy pracował niewolniczo wraz z
        innymi Polakami, jak on wywiezionymi w głąb Związku Sowieckiego w 1940 roku: „Bracia Polacy, jesteście naszymi
        przyjaciółmi. Wspólnie mamy walczyć przeciwko Niemcom. Od tego dnia jesteście ludźmi wolnymi, jesteście
        traktowani,
        jak nasi sprzymierzeńcy”. Wczorajsi wrogowie, pozbawieni wszelkich praw zesłańcy, zmienili się nagle w
        sojuszników,
        którzy otrzymali szansę walki z bronią w ręku i powrotu do kraju. To o nich generał Władysław Sikorski
        mówił do
        swego przyjaciela, generała Mariana Kukiela: „Gdy miałem ten układ podpisać i walczyłem ze sobą, czy odczekać
        jeszcze, słyszałem jakby szept tysięcy ust: spiesz się, ratuj!”. To miała być też odpowiedź dla tych, którzy
        krytykowali Naczelnego Wodza i premiera za zbyt pochopne podpisanie
        paktu z Sowietami
      </p>

      <h4 class="fw-bold">Wyczekiwana wojna</h4>

      <p>Na wieść o niemieckiej agresji na Związek Sowiecki 22 czerwca 1941 roku premier Wielkiej Brytanii Winston
        Churchill
        odetchnął z ulgą. Hitler, dając rozkaz do ataku na swego sojusznika, popełnił od dawna wyczekiwany na Zachodzie
        błąd
        – rozpoczął wojnę na dwa fronty, a jego przeciwnikiem stał się największy kraj na świecie o olbrzymim potencjale
        ludzkim. Jeszcze tego samego dnia wieczorem Churchill oświadczył, że „państwo podejmujące z determinacją walkę
        przeciwko III Rzeszy, wspólnemu wrogowi wszystkich narodów, staje się automatycznie, bez zastrzeżeń, brytyjskim
        sojusznikiem”. Dla Polski w tym zdaniu najważniejsze i najgroźniejsze zarazem było stwierdzenie „bez zastrzeżeń”.
        Niewątpliwie jednak rozbicie sojuszu niemiecko-sowieckiego było dla Polaków wydarzeniem pozytywnym, niosącym
        nadzieję, że śmiertelni wrogowie wykrwawią się na tyle, że znowu będzie możliwe odzyskanie niepodległości. Trzeba
        też pamiętać o tym, że powszechnie sądzono wtedy, iż Armia Czerwona nie sprosta Wehrmachtowi i rozsypie się
        całkowicie pod jego uderzeniem. Wiele na to wskazywało po pierwszych bitwach granicznych. Ostateczne zwycięstwo
        nad
        Niemcami, podobnie jak w I wojnie światowej, miało przynieść rozstrzygające uderzenie aliantów na Zachodzie.
        Najważniejsze było, że ogromne armie niemieckie związały się walką na Wschodzie, więc Rosji należało udzielić
        wszelkiej pomocy i poparcia. 12 lipca 1941 roku w Moskwie został podpisany przez Wiaczesława Mołotowa i ambasadora
        brytyjskiego w ZSRS Stafforda Crippsa układ sowiecko-brytyjski, a 18 lipca zakończono rokowania
        sowiecko-czechosłowackie. Także zza oceanu pobiegły w stronę Kremla słowa poparcia. Już 23 czerwca 1941 roku, w
        odpowiedzi na deklarację Churchilla, amerykański podsekretarz stanu Sumner Welles zaoferował Stalinowi poparcie
        Stanów Zjednoczonych.</p>

      <h4 class="fw-bold"> Pakt z diabłem</h4>

      <p>Tego samego dnia, w którym Welles zadeklarował pomoc swego kraju dla Związku Sowieckiego, w radiu przemówienie
        wygłosił generał Władysław Sikorski. Szef rządu RP na uchodźctwie zasugerował możliwość poprawy stosunków
        polsko-sowieckich. Zasugerował przy tym, że Polska wstępnie widzi potrzebę zmiany stanowiska Kremla w kwestii
        granic, czyli powrotu do granicy ustalonej w traktacie ryskim i uwolnienia z sowieckich więzień i obozów
        przetrzymywanych tam obywateli polskich. To otworzyło drogę do roboczych rozmów między niedawnymi wrogami, których
        głównym pośrednikiem został minister spraw zagranicznych Wielkiej Brytanii, Anthony Eden. Polsko-sowieckie
        negocjacje zainicjowano 5 lipca 1941 roku w gmachu Foreign Office. Ze strony polskiej brał w nich udział generał
        Władysław Sikorski i minister spraw zagranicznych RP, August Zaleski. Rząd sowiecki reprezentował ambasador ZSRS w
        Londynie, Iwan Majski. Rozmowy protokołował Aleksander Cadogan, podsekretarz stanu Foreign Office. Po kolejnym
        spotkaniu obu stron, do którego doszło 11 lipca, Majski zadeklarował, że przekaże swemu rządowi polskie postulaty
        ujęte w pięciu punktach: „1. Rząd radziecki wypowie traktaty z Niemcami, z sierpnia i września 1939 r.; 2. Nastąpi
        normalizacja stosunków między obydwoma rządami i akredytacja polskiego ambasadora w Moskwie; 3. Polska będzie
        gotowa
        współpracować we wspólnej walce przeciwko Niemcom; 4. Utworzenie specjalnej Armii, jako oddzielnej jednostki na
        terytorium radzieckim, która mogłaby być przeniesiona gdzie indziej, jeśliby sobie tego strona polska życzyła.
        Dokładny status Armii Polskiej odpowiadałby statusowi Polskich Sił Zbrojnych w Zjednoczonym Królestwie Wielkiej
        Brytanii i Północnej Irlandii; 5. Zwolnienie internowanych żołnierzy polskich i więźniów politycznych”.<br><br>
        Podpisanie układu, Londyn, 30 lipca 1941. Od lewej: Władysław Sikorski, Anthony Eden, Winston Churchill i Iwan
        Majski.
      </p>
      <p>Znamienne, że nie znalazł się tutaj postulat dotyczący granic, o którym generał Sikorski mówił w przemówieniu
        radiowym. Wywołało to burzę na posiedzeniu rządu RP. Do dymisji podało się trzech jego ministrów: generał
        Kazimierz
        Sosnkowski, August Zaleski i Marian Seyda. Negatywne stanowisko wyraził również Prezydent RP Władysław
        Raczkiewicz.
        Prezydent nawoływał do zerwania rozmów z Sowietami i zadeklarował, że nie podpisze umowy w takim kształcie, co
        zresztą później uczynił. W proteście doszło również do rozwiązania Rady Narodowej RP, pełniącej rolę polskiego
        parlamentu na uchodźstwie. Kryzys gabinetowy nie zmusił jednak generała Sikorskiego do przerwania rokowań, tym
        bardziej że poddany był on silnej presji ze strony Brytyjczyków. Na Sikorskiego, o czym dziś się zapomina,
        naciskał
        również mocno rząd czechosłowacki, co związane było z rozmowami o przyszłej „polsko-czechosłowackiej unii” po
        zakończeniu wojny.
      </p>
      <p>Mimo braku potwierdzenia expressis verbis granicy polsko-sowieckiej ustalonej  w traktacie
        ryskim, generał Sikorski
        30 lipca 1941 roku zdecydował się parafować pakt polsko-sowiecki, który od nazwisk sygnatariuszy przeszedł do
        historii jako pakt lub układ Sikorski-Majski. Wobec odmowy prezydenta Raczkiewicza podpisania go, formalnie
        pozostawał on zwykłym protokołem. Nie ogłoszono go również w Dzienniku Ustaw, ani nie zarejestrowano w
        Sekretariacie
        Ligi Narodów. W tamtym momencie miało to jednak drugorzędne znaczenie. Sowieci w pierwszym punkcie układu uznali
        za
        niebyłe traktaty sowiecko-niemieckie z 1939 roku; w drugim potwierdzono nawiązanie wzajemnych stosunków
        dyplomatycznych (polski ambasador w ZSRS Stanisław Kot przybył do Moskwy 4 września 1941 roku); w trzecim obie
        strony zadeklarowały wzajemną pomoc w wojnie z Niemcami; w czwartym czwartym rząd
          sowiecki zgadzał się na formowanie na swym terytorium polskiej armii. W dodatkowym protokole do paktu
        Sowieci
        zobowiązywali się, że po przywróceniu stosunków
        dyplomatycznych udzielona zostanie amnestia wszystkim obywatelom polskim pozbawionym wolności w ZSRS.</p>
      <p>Przeciwnicy podpisania układu wytykali generałowi Sikorskiemu, że zaprzepaścił granice wschodnie. Podkreślali nie
        bez racji, że mógł bardziej twardo negocjować z Sowietami, którzy w lipcu byli w rozpaczliwej sytuacji na froncie
        i
        przez to skłonni do ustępstw jak nigdy wcześniej. Prof. Paweł Wieczorkiewicz przywołuje świadectwo generała
        Mariana
        Januszajtisa-Żegoty, który w czasie kiedy toczyły się polsko-sowieckie rozmowy przebywał jeszcze w więzieniu na
        Łubiance. O negocjacjach dowiedział się od samego Ławrentija Berii. Szef NKWD w czasie przesłuchań zaprosił
        generała
        do dyskusji na ten temat. Prof. Wieczorkiewicz zauważył: „Januszajtis sądził, że Sikorski w żadnym wypadku nie
        powinien zgodzić się na podpisanie układu bez gwarancji granicznych – a tak go podpisano. Beria twierdził, że
        Polacy
        jednak ten układ podpiszą i potem triumfował, że tak właśnie się stało. Oświadczył […], że Sowieci podpisaliby
        gwarancje graniczne, bo znajdowali się w sytuacji przymusu. Sikorski nie wyczuł tego momentu”. Jednak profesor od
        razu zauważył, że jeśli doszłoby do gwarancji granicznych, to i tak Stalin mógłby je potem złamać – nie takie
        umowy
        Sowieci łamali. Poza tym polski premier nie miał w tej mierze żadnego wsparcia ze strony zachodnich
        sojuszników.
      </p>
      <p>Brytyjski rząd podkreślał cały czas, że gwarantował Rzeczypospolitej jedynie niepodległość, a nie granice.
        Podobne
        stanowisko zajęli Amerykanie. Sumner Welles dzień po podpisaniu polsko-sowieckiego paktu wyraził się o nim
        entuzjastycznie, ale w jego wypowiedzi nie pojawiło się ani jedno słowo o wschodniej granicy Polski.
      </p>
      <h4 class="fw-bold">Wyrwani z nieludzkiej ziemi</h4>

      <p>Jednak niezaprzeczalnym sukcesem Sikorskiego był ten dodatkowy protokół do układu, w którym była mowa o amnestii
        polskich obywateli w ZSRS. Tym rzeczywiście ocalił tysiące Polaków nie tylko nadal więzionych po niemieckim
        najeździe, ale i mordowanych każdego dnia w czasie ewakuacji więzień NKWD w głąb sowieckiego państwa. Jednak samo
        słowo „amnestia” wywołało wśród Polaków na Zachodzie wielkie kontrowersje i oburzenie. Podkreślano, że ani jeńcy
        wojenni, ani deportowani czy więzieni Polacy w Związku Sowieckim nie popełnili żadnych przestępstw, które
        upoważniałyby władze sowieckie do ich amnestionowania. Jednak najważniejsze było to, że dano tym ludziom szansę
        wydostania się z sowieckiego piekła. Jak ujął to Karol Popiel (chadecki polityk, na uchodźstwie): „Ponad
        nieskazitelność formuły prawnej wysuwa się sprawa losu ginących na olbrzymich obszarach Związku Sowieckiego mas
        zesłańców, jeńców i więźniów”. Można dyskutować, czy generał Sikorski nie mógł przeciągać negocjacji, ale należy
        pamiętać, że z każdą godziną, każdym dniem na nieludzkiej ziemi ginęli kolejni Polacy. Krytycy generała wychodzili
        wtedy również z założenia, że godziny Armii Czerwonej są już policzone i z Sowietami trzeba rozmawiać twardo i bez
        ustępstw. Kolejne lata wojny pokazały, że się mylili, a generał Władysław Anders mógł wyprowadzić z sowieckiej
        Rosji
        zarówno swą armię, jaki i tysiące cywilów, którzy niechybnie by tam wymarli, gdyby nie pakt Sikorski-Majski. W
        dyskusji o nim zawsze należy mieć w pamięci słowa cytowanego na początku Antoniego Łapińskiego i wielu innych,
        którym udało się przeżyć to piekło i później walczyć i żyć dla Polski.</p>

