    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">The Sikorski-Majski Pact: success or a missed opportunity?</h4>

      <p>
        <b>Colonel Antoni Łapiński, an exile and a soldier of the 2nd Polish Corps, remembered well the day when he learned about the signing of the Polish-Soviet pact on July 30, 1941: “For us it was a miracle, it was like a resurrection, but most of all it was an extra slice of bread. An unimaginable treasure for those suffering from hunger. "</b>
      </p>
      <p>
        Antoni Łapiński also recalls the speech of the construction manager, during which he had been working as a slave for several months with other Poles, such as himself, deported deep into the Soviet Union in 1940: “Brothers, Poles, you are our friends. We are to fight against Germany together. From that day on, you are free people, you are treated like our allies ”. Yesterday's enemies, the exiles deprived of all rights, suddenly turned into allies who were given a chance to fight with weapons in hand and return to their country. It was about them that General Władysław Sikorski spoke to his friend, General Marian Kukiel: "When I was about to sign this treaty and I was fighting with myself or waiting, I heard a whisper of thousands of lips: hurry up, save!". It was also supposed to be a response to those who criticized the Commander-in-Chief and the Prime Minister for too hasty signing the pact with the Soviets
      </p>

      <h4 class="fw-bold">

The long-awaited war
</h4>

      <p>At the news of German aggression against the Soviet Union on June 22, 1941, British Prime Minister Winston Churchill breathed a sigh of relief. Hitler, giving the order to attack his ally, committed a mistake long awaited in the West - he started a war on two fronts, and his opponent became the largest country in the world with enormous human potential. Later that evening, Churchill declared that "a state which waged a determined struggle against the Third Reich, the common enemy of all nations, would automatically become a British ally without reservation." For Poland, in this sentence the most important and the most dangerous at the same time was the phrase "without reservations". Undoubtedly, however, the break-up of the German-Soviet alliance was a positive event for Poles, bringing hope that their mortal enemies would bleed enough to regain independence again. It should also be remembered that it was commonly believed then that the Red Army would not be able to cope with the Wehrmacht and would collapse completely under its impact. There were many indications of this after the first border battles. The final victory over Germany, as in the First World War, was to bring a decisive attack on the Allies in the West. The most important thing was that the huge German armies were engaged in fighting in the East, so Russia had to be given all the help and support. On July 12, 1941, the Soviet-British pact was signed in Moscow by Vyacheslav Molotov and the British ambassador to the USSR, Stafford Cripps, and on July 18 the Soviet-Czechoslovak negotiations ended. Words of support also ran towards the Kremlin from overseas. As early as June 23, 1941, in response to Churchill's declaration, US Undersecretary of State Sumner Welles offered Stalin the support of the United States.</p>

      <h4 class="fw-bold"> Pact with the devil</h4>

      <p>On the same day that Welles declared his country's aid to the Soviet Union, General Władysław Sikorski delivered a speech on the radio. The head of the Polish government-in-exile suggested the possibility of improving Polish-Soviet relations. He also suggested that Poland initially saw the need to change the Kremlin's position on the border issue, i.e. return to the border established in the Treaty of Riga and release Polish citizens from Soviet prisons and camps. This paved the way for working talks between recent enemies, with the UK Foreign Minister Anthony Eden being the main intermediary. Polish-Soviet negotiations were initiated on July 5, 1941 in the Foreign Office building. On the Polish side, General Władysław Sikorski and the Minister of Foreign Affairs of the Republic of Poland, August Zaleski, took part in them. The Soviet government was represented by the ambassador of the USSR in London, Ivan Mayski. The talks were recorded by Aleksander Cadogan, Undersecretary of State of the Foreign Office. After the next meeting of both sides, which took place on July 11, Majski declared that he would communicate to his government Polish demands included in five points: “1. The Soviet government terminated the August and September 1939 treaties with Germany; 2. The relations between the two governments will normalize and the Polish ambassador to Moscow will be accredited; 3. Poland will be ready to cooperate in the common fight against Germany; 4. The creation of a special Army as a separate unit on Soviet territory, which could be moved elsewhere if the Polish side so wished. The exact status of the Polish Army would correspond to that of the Polish Armed Forces in the United Kingdom of Great Britain and Northern Ireland; 5. Release of interned Polish soldiers and political prisoners ”.<br><br>
        Signing the agreement, London, July 30, 1941. From the left: Władysław Sikorski, Anthony Eden, Winston Churchill and Iwan Majski.
      </p>
      <p>It is significant that the border postulate that General Sikorski spoke about in his radio speech was not included here. It caused a storm at the meeting of the Polish government. Three of his ministers resigned: General Kazimierz Sosnkowski, August Zaleski and Marian Seyda. The President of the Republic of Poland, Władysław Raczkiewicz, also expressed a negative position. The president called for breaking talks with the Soviets and declared that he would not sign the agreement in this form, which he did later. The protest also saw the dissolution of the National Council of the Republic of Poland, which was the Polish parliament in exile. The cabinet crisis, however, did not force General Sikorski to stop the negotiations, all the more so as he was subjected to strong pressure from the British. Sikorski, which is being forgotten today, was also heavily pressed by the Czechoslovak government, which was related to the talks about the future "Polish-Czechoslovak union" after the end of the war.


      </p>
      <p>Despite the lack of expressis verbis confirmation of the Polish-Soviet border established in the Treaty of Riga, on July 30, 1941, General Sikorski decided to initiate the Polish-Soviet pact, which went down in history from the names of the signatories as the Sikorski-Mayski pact or pact. In view of President Raczkiewicz's refusal to sign it, formally it remained an ordinary protocol. It was not published in the Journal of Laws, nor was it registered with the Secretariat of the League of Nations. At the time, however, it was of secondary importance. In the first point of the treaty, the Soviets considered the 1939 Soviet-German treaties invalid; the second confirmed the establishment of mutual diplomatic relations (Polish ambassador to the USSR, Stanisław Kot, arrived in Moscow on September 4, 1941); in the third, both sides declared their mutual aid in the war with Germany; in the fourth, fourth, the Soviet government agreed to form a Polish army on its territory. In an additional protocol to the pact, the Soviets undertook that after the restoration of diplomatic relations, amnesty would be granted to all Polish citizens deprived of their liberty in the USSR.

</p>
      <p>Opponents of signing the treaty pointed out to General Sikorski that he had lost the eastern borders. They emphasized rightly that he could negotiate more hard with the Soviets, who in July were in a desperate situation at the front and therefore willing to make concessions like never before. Prof. Paweł Wieczorkiewicz cites the testimony of General Marian Januszajtis-Żegota, who was still in prison in Lubyanka at the time when the Polish-Soviet talks were taking place. He learned about the negotiations from Lavrenti Beria himself. During the interrogations, the head of the NKVD invited the general to discuss this topic. Prof. Wieczorkiewicz noted: “Januszajtis thought that Sikorski should under no circumstances agree to sign the agreement without border guarantees - and this is how it was signed. Beria claimed that the Poles, however, would sign this agreement and then triumphed that this was what had happened. He stated […] that the Soviets would have signed border guarantees because they were in a situation of coercion. Sikorski did not sense this moment. However, the professor immediately noted that if there were limit guarantees, Stalin could break them anyway - these were not the agreements the Soviets broke. Moreover, the Polish prime minister did not have any support in this respect from Western allies.
      </p>
      <p>The British government emphasized all the time that it guaranteed the Republic of Poland only independence, not borders. The Americans took a similar position. Sumner Welles, a day after the signing of the Polish-Soviet pact, expressed enthusiasm about it, but his statement did not contain a single word about the eastern border of Poland.
      </p>
      <h4 class="fw-bold">Uprooted from inhuman land</h4>

      <p>However, Sikorski's undeniable success was this additional protocol to the treaty, which referred to the amnesty of Polish citizens in the USSR. In this way he saved thousands of Poles not only still imprisoned after the German invasion, but also murdered every day during the evacuation of NKVD prisons deep into the Soviet state. However, the very word "amnesty" caused great controversy and indignation among Poles in the West. It was emphasized that neither prisoners of war, nor the deported or imprisoned Poles in the Soviet Union committed any crimes that would authorize the Soviet authorities to amnesty them. However, the most important thing was that these people were given a chance to get out of Soviet hell. As Karol Popiel (Christian Democrat politician, in exile) put it: "Above the impeccability of the legal formula is the question of the fate of the masses of exiles, prisoners and prisoners dying in vast areas of the Soviet Union." It is debatable whether General Sikorski could delay the negotiations, but it should be remembered that every hour and every day more Poles were dying on inhuman soil. The general's critics also assumed that the hours of the Red Army were already numbered and that one had to talk toughly and without concessions with the Soviets. The subsequent years of the war proved that they were wrong, and General Władysław Anders could lead from Soviet Russia both his army and thousands of civilians who would have died there, if not for the Sikorski-Mayski Pact. When discussing him, one should always remember the words of Antoni Łapiński, quoted at the beginning, and many others who managed to survive this hell and then fight and live for Poland.</p>

